.navBarBox {
  display: flex;
  background-color: rgba(67, 0, 97, 255);
  position: sticky;
  width: 100%;
  top: 0;
  border-color: #ffffff;
  border-bottom: 0px solid #ccc;
  box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px,
    rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px,
    rgba(0, 0, 0, 0.09) 0px 32px 16px;
  z-index: 1000;
}

.navBarBoxLeft {
  max-width: 400px;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.easy {
  margin-top: 8px;
  margin-bottom: 0;
  text-shadow: 0px 15px 40px rgba(0, 0, 0, 0.35);
  font-family: Kanit;
  font-size: 36px;
  font-style: normal;
  font-weight: 700;
  line-height: 25px;
  color: white;
}

.qr {
  margin-top: 0;
  margin-bottom: 8px;
  background: linear-gradient(180deg, #ffffff 45%, #828184 55%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: Kanit;
  font-size: 50px;
  font-style: normal;
  font-weight: 700;
  line-height: 60px;
}

.navBarBoxCenter {
  flex: 1;
}

.navBarBoxRight {
  flex: 2;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
}

.navButton {
  background-color: rgba(67, 0, 97, 255);
  border-color: white;
  color: white;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-right: 15px;
  padding-left: 15px;
  cursor: pointer;
  border-radius: 8px;
  border-width: 0;
  transition: background-color 0.5s, color 0.5s;
  font-weight: bold;
  margin: 0 10px;
}

.navButton:hover {
  background-color: white;
  color: rgba(67, 0, 97, 255);
}

/*=========side bar=========*/

.menuButton {
  display: none;
  background: none;
  border: 8px;
  color: white;
  font-size: 24px;
  cursor: pointer;
  position: fixed;
  top: 10px;
  right: 10px;
  transition: background-color 0.5s, color 0.5s;
}

.menuButton:hover {
  background-color: white;
  color: rgba(67, 0, 97, 255);
  font-weight: bold;
  opacity: 1;
}

.sidebar {
  display: none;
  position: fixed;
  top: 8px;
  right: 8px;
  height: 50%;
  width: 40%;
  /* background-color: #716d6d; */
  padding: 20px;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.3);
  z-index: 1000;
  background-color: none;
  background-color: rgba(0, 0, 0, 0.5);
}

.sidebar.open {
  display: block;
}

.closeButton {
  background: none;
  border: none;
  color: white;
  font-size: 20px;
  cursor: pointer;
  margin-bottom: 20px;
  transition: background-color 0.5s, color 0.5s;
}

.closeButton:hover {
  background-color: rgb(244, 41, 41, 2);
  color: white;
  border-radius: 5px;
}

.sidebarButton {
  background: none;
  border: none;
  color: white;
  padding: 10px;
  text-align: center;
  width: 100%;
  cursor: pointer;
  margin: 10px auto;
  z-index: 1002;
  transition: background-color 0.5s, color 0.5s;
}

.sidebarButton:hover {
  background-color: white;
  color: rgba(67, 0, 97, 255);
  font-weight: bold;
  opacity: 1;
  border-radius: 8px;
}

@media screen and (max-width: 400px) {
  .navBarBoxRight {
    flex-wrap: wrap;
  }
}

@media (max-width: 768px) {
  .navBarBoxRight {
    display: none;
  }
  .navBarBoxCenter {
    display: none;
  }
  .navBarBoxLeft {
    top: 10px;
    left: 20px;
    position: absolute;
  }
  .menuButton {
    display: block;
    position: absolute;
  }
  .sidebar {
    display: none;
  }
  .sidebar.open {
    display: block;
  }
}
